.skills{
    margin-top: 50px;
    padding: 0px 80px;
}
.arrow-icon{
    color: rgb(23, 84, 86);
}

.carousel-control-prev-icon {
    background-image: url("./arrow-l.png") !important;
   }
.carousel-control-next-icon {
    background-image: url("./arrow-r.png") !important;
   }  

.carousel-indicators button:active{
    background-color: chocolate;

}


.skills-main p{
    width: 50%;
    text-align: center;
    justify-content: center;
    margin: 0px auto;
    
}
.skills-main{
    width: 90%;
    
    padding-top: 30px;
    
    
    border-radius: 30px;
}
.skillMain-light{
    
    background-color: #e3e3e33b;
     box-shadow: 0px -15px 15px rgba(91, 92, 92, 0.2);
}
.skillMain-dark{
    width: 90%;
    background-color: rgba(238, 238, 238, 0.236);
    box-shadow: 0px -15px 15px rgba(167, 168, 168, 0.2);
}
