.spl-main{
    margin-top: 100px !important;
    border-radius: 30px;
    width: 90%;
    padding-top: 15px;
    margin-bottom: 20px;

    box-shadow: 0px -15px 15px rgba(167, 168, 168, 0.2);
    background-image: linear-gradient(to bottom, #dfdfdf1b, #e7e7e722, #efefef17, #f7f7f711, #ffffff00);
    
    scroll-behavior: smooth;
}
.sp-head h2{
    font-weight: 700;
    color: rgb(141, 140, 140)
}
.sp-head-quote p{
    width: 35%;
    text-align: center;
}
.sp-card{
    
    width: 18rem;
    
    
    margin: 0px 10px 20px 10px;
    box-shadow: 20px 20px 50px rgba(0,0,0,0.5);
    border-radius: 15px;
    background: rgba(183, 183, 183, 0.1);
    border: 0px;
    backdrop-filter: blur(5px);
    transition: all 0.4s ease;
    justify-content: center;
    align-items: center;
    overflow: hidden;


}



.card-h{
    z-index: 2;
}

.card-img-top{
    width: 35%;
    height: 7rem;
   
    
}


@media only screen and (max-width: 400px) {
    .sp-head-quote p{
        width: 85%;
    }
    .spl-main{
        margin-top: 15% !important;
    }
}
.sptext-dark{
    color: grey;
}
.sptext-light{
    color: rgb(181, 178, 178);
}
.sptitle-dark{
    color: rgb(0, 155, 226);
}
.sptitle-light{
    color: rgb(141, 140, 140); 
}