.hm-main-light{
  background-color: whitesmoke !important;
  border-radius: 30px !important;
 
  margin: 0% !important;

  
 
   
}
.hm-main-dark{
  background-image: linear-gradient(to right bottom, #000000, #272727, #494949, #6e6e6e, #959595);
  
  border-radius: 30px !important;
  margin: 0% !important;

  
 
   
}

@media only screen and (max-width: 400px) {
  .hm-main{
   
  }
}

  
  .hm-head h2 {
  
    letter-spacing: 0px;
    word-spacing: 10px;
    font-size: 220% !important;
    background-image: linear-gradient(to left top, #0b3e8b, #a8409b, #ff5278, #ff9e39, #fff906);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  
  
  
  .ct-quote-span:hover {
    cursor: pointer;
    color: coral;
  }
  
  .contactWidth {
    margin: -10% 10% 0% 10%;
  
  }
  
  .contact-gif {
  
    padding-left: 12% !important;
  
  }
  
  .contactTouchHead {
    text-align: center;
    margin-left: 8%;
    background-image: linear-gradient(to left top, #d8d8d8, #ffd2f0, #ffc6ca, #ffd478, #fbff2a);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  
  
  }
  
  .contactLine {
    background-color: grey;
    height: 1px;
    width: 70%;
    margin-left: 8%;
    margin-bottom: 10px;
  
  }
  
  .contactAnim {
    width: 70%;
  
  }
  
  @media only screen and (max-width: 400px) {
  
    .contactAnim {
      margin-top: -30%;
      width: 100%;
    }
  
    .ct-head h2 {
      text-align: center;
      font-size: 30px !important;
    }
    .InGetInTouch{
    
      width: 135%;
      margin-left: -20% !important;
  
    }
    .SendMsgform{
      width: 120% !important;
      margin-left: -15%;
      margin-top: -40% !important;
      margin-bottom: 30%;
    }
  }
  
  .InGetInTouch {
    margin-left: 7%;
    border-radius: 20px;
    padding: 1% 1% 0% 5% !important;
  
    box-shadow: -3px -4px 15px 2px rgba(167, 168, 168, 0.2);
  
  
  }
  
  .InGetInTouch p {
    color: rgb(160, 160, 160);
    padding-bottom: 5px;
  
  
  }
  
  .addC {
    text-align: center;
  }
  
  .InGetInTouch p i {
  
    padding: 7px;
    margin: 4px;
    border-radius: 45%;
    background-color: #e6e6e6;
  }
  
  .locationTag {
    width: 80%;
  }
  
  .SendMsgform {
    background-color: rgba(187, 187, 187, 0.195);
    padding: 10px;
    border-radius: 12px;
    width: 65%;
    margin-top: 5%;
    box-shadow: -7px -10px 15px #9497955d;
  }
  
  .msgInputGrp {
    padding: 10px;
  }
  
  .msgInputGrp input {
    box-shadow: 0 0 15px 4px rgba(0, 0, 0, 0.06);
    background-color: none;
  }
  
  .sendMsg {
    background-color: #6EE729;
    width: 95%;
    color: white;
    font-weight: 500;
    margin: 10px;
    position: relative;
    transition: 0.3s all ease-in-out;
  
  }
  
  .sendMsg:hover {
    background-color: #ff5500;
    color: white;
    z-index: 20000;
  }
  
  .msgComment {
    width: 100%;
    border-radius: 15px;
    height: 152px;
    border: none;
  }
  
  .formP {
    text-align: center;
    margin-top: 1%;
    margin-bottom: 1%;
    font-size: 12px;
  }
  
  
  .msgicon {
    position: absolute;
    padding-left: 10px;
    padding-top: 3px;
    z-index: 100;
    scale: 1.5;
  }
  
  .comicon {
    position: absolute;
    padding-left: 10px;
    padding-top: 6px;
    z-index: 100;
    scale: 1.5;
  }
  
  
  
  
  
  
  
  .msgInputGrp input {
    padding-left: 35px !important;
    position: relative;
    font-family: "Source Sans Pro", sans-serif;
    font-weight: 400;
    color: grey;
  
    width: 100%;
    height: 40px;
    border: none;
    padding: 0 10px;
    box-shadow: none;
    outline: none;
  
    border: 2px solid rgba(0, 0, 0, .15);
    box-shadow: 0 20px 0 0 #d5d5d5 inset, 0 -20px 0 0 #d5d5d5 inset;
    -webkit-transition: all 1s ease;
    -moz-transition: all 1s ease;
    -ms-transition: all 1s ease;
    -o-transition: all 1s ease;
    transition: all 1s ease;
  }
  
  .msgInputGrp input:focus {
    box-shadow: none;
  
    border-color: rgba(0, 0, 0, .3);
  }
  
  .msgComment {
    padding-left: 30px !important;
    padding-top: 1px !important;
    position: relative;
    font-family: "Source Sans Pro", sans-serif;
    font-weight: 400;
    color: grey;
    resize: none;
    width: 100%;
    height: 80px;
    border: none;
    padding: 0 10px;
    box-shadow: none;
    outline: none;
    border: 2px solid rgba(0, 0, 0, .15);
    box-shadow: 0 76px 0 0 #d5d5d5 inset, 0 -76px 0 0 #d5d5d5 inset;
    -webkit-transition: all 0.5s ease;
    -moz-transition: all 0.5s ease;
    -ms-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;
    transition: all 0.5s ease;
  }
  
  .msgComment::placeholder,
  .msgInputGrp input::placeholder {
    font-weight: 300;
  
  
  }
  
  .msgComment:focus {
    box-shadow: none;
    border-color: rgba(0, 0, 0, .3);
  }
  
  .Cfname,
  .Clname {
    color: skyblue !important;
  }
  
  .Cmob {
    color: coral;
  }
  .hm-main{
    background-color: rgb(255, 255, 255);
    border-radius: 20px;
  }
  .cross-icn{
    font-size: 150%;
   float: right;
   position: relative;
   right: 0;
   top: 6px;
   
   
  }
  .cross-icn:hover{
    font-weight: 900;
    scale: 1.2;
    color: rgb(255, 80, 32);
    
    
  }

