@import url('https://fonts.googleapis.com/css2?family=Bruno+Ace&display=swap');


.hnav{
   
    
    
    font-weight: 500;
    letter-spacing: 1px;
    background: linear-gradient(to right,#ccc 50%, #ff5858 50%, #cc2e5d);
    background-size: 200%;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    transition: 0.5s ease-out;
    padding-top: 10px !important;
    margin-bottom: -10px !important;

}
.logoGif{
   width: 70px;
   height: 70px;
   margin: -25px;
   margin-right: -5px;
   
}

.nav-topbar-light{
    top: 0px;
    background:  #EDF7FF;
    margin: auto;
    width: 95%;
    box-shadow: 0px 0px 15px 15px rgba(167, 168, 168, 0.521);
   border-radius: 0px 0px 30px 30px;
   z-index: 10000;
   transition: all 0.3s ease-in-out;
}

.nav-topbar-dark{
    top: 0px;
    background: #000000;
    margin: auto;
    width: 95%;
    box-shadow: 0px 0px 15px 15px rgba(103, 104, 104, 0.295);
   border-radius: 0px 0px 30px 30px;
   z-index: 10000;
   transition: all 0.3s ease-in-out;
}
.navbar-brand{
    color: #B7A7A7;
}

@media only screen and (max-width: 400px) {
    
    .hnav:hover{
        scale: 1 !important;
   
    
    
    
    }
  }



.nav-link:hover,
.nav-link.active{
    cursor: pointer;
    background-position: -100%;
}

