
.al-main{
    width: 80%;
    align-items: center;
    justify-content: center;
    margin-top: 5%;
    margin-bottom: 3%;
    
    scroll-behavior: smooth;
}
.sec-al{
    margin-top: 5%;
}
.al-card{
   
    width: 320px;
    height: 200px;
    background: #ffffff27 !important;
    border-radius: 20px;
    box-shadow: 0 35px 80px rgba(0,0,0,0.15);
    transition: 0.5s;
    overflow: visible;
    border: 0px;
    
}
.al-card:hover{
   
    height: 300px;
}
.al-img{
    width: 100px;
    height: 120px;
    margin: auto;
    margin-top: -50px;
    border-radius: 20px;
    box-shadow: 0 15px 50px rgba(0,0,0,0.35);
    transition: 0.5s;
}
.rpk-img{
    background-color: yellow;
    background: url("./rpkimg.png");
    background-size:100px;
    background-position: center;
    background-repeat: no-repeat;
}
.ttt-img{
    background: url("./tttimg.png");
    background-size:100px;
    background-position: center;
    background-repeat: no-repeat;
}

.al-card:hover .rpk-img{
    width: 100px;
    height: 192px;
    background: url("./rpk_gif.gif");
    background-size: contain;
    background-position: center;
   
}

.itzone-img{
    background: url("./itzonepc.png");
    background-size:100px;
    background-position: center;
    background-repeat: no-repeat;
    
}
.al-card-wide:hover .itzone-img{
    width: 180px;
    height: 100px;
    background: url("./itzone_gif.gif");
    background-size: contain;
    background-position: center;
   
}
.itzone-app{
    background: url("./itzone_app_img.png");
    background-size:100px;
    background-position: center;
    background-repeat: no-repeat;
    
}
.al-card:hover .itzone-app{
    width: 100px;
    height: 192px;
    background: url("./itzone_app_gif.gif");
    background-size: contain;
    background-position: center;
   
}
.rpk-py{
    background: url("./rpk_py.png");
    background-size:100px;
    background-position: center;
    background-repeat: no-repeat;
    
}
.al-card-wide:hover .rpk-py{
    width: 100px;
    height: 100px;
    background: url("./rpkpy.gif");
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
   
}
.rpk-h2{
    font-weight: 600;
    font-size: 20px;
   text-align: center;
   margin-top: -40px;
   background-image: linear-gradient(to right, #ed3493, #ff4376, #ff5f5a, #ff7f3e, #ff9e26, #e6b518, #c8c829, #a6d949, #66dc7c, #00daab, #00d4d0, #00cae5);
   -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  transition: 0.5s ease-out;
}
.itzone-h2{
    font-weight: 600;
    font-size: 20px;
   text-align: center;
   margin-top: -40px;
   background-image: radial-gradient(circle, #eb61a6, #cb84e2, #8ea8ff, #3ec4ff, #00d9fe, #00e3eb, #3fead1, #77eeb3, #92ef98, #b0ed7c, #d2e962, #f5e24d);
   -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  transition: 0.5s ease-out;
}

.al-card-wide{
   
    width: 320px;
    height: 190px;
    background: #ffffff27 !important;
    border-radius: 20px;
    box-shadow: 0 35px 80px rgba(0,0,0,0.15);
    transition: 0.5s;
    overflow: visible;
    border: 0px;
}
.alc-dark{
    box-shadow: 0 35px 80px rgba(255, 255, 255, 0.15) !important;
}
.al-card-wide:hover{
   
    height: 220px;
}
.rpk-py-h2{
    font-weight: 600;
    font-size: 20px;
   text-align: center;
   margin-top: -40px;
   background-image: radial-gradient(circle, #bebebe, #c3aac9, #de8dbb, #f86b90, #ff4f4f);
   -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  transition: 0.5s ease-out;
}
.itzone-app-h2{
    font-weight: 600;
    font-size: 20px;
   text-align: center;
   margin-top: -40px;
   background-image: radial-gradient(circle, #bebebe, #c3aac9, #de8dbb, #f86b90, #ff4f4f);
   -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  transition: 0.5s ease-out;
}
.ttt-h2{
    font-weight: 600;
    font-size: 20px;
   text-align: center;
   margin-top: -40px;
   background-image: linear-gradient(to right, #54f0ff, #6bdee8, #7accd2, #83babd, #89a8a9, #89a9a9, #89a9aa, #89aaaa, #84bebd, #7bd2cf, #70e6e0, #5ffbf1);
   -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  transition: 0.5s ease-out;
}

@media only screen and (max-width: 400px) {
    .al-main{
     
     margin-top: 30%;
     margin-bottom: 20%;
    }
    .al-card{
        margin-bottom: 20%;
    }
    .al-card-wide{
        margin-top: 25%;

    }
    
  }