.mp-main{
  padding: 20px;
  width: 80%;
  border-radius: 30px;
  box-shadow: 0px -15px 15px rgba(167, 168, 168, 0.2);

    scroll-behavior: smooth;
}
.mp-nav{
    background:none;
   
}
.mp-link{
  
    margin: 0px 20px 0px 20px;
    color: #9e9e9e;
   
}
.mp-link:hover{
  color: rgb(114, 114, 114) !important;
  scale: 1;
}
@media only screen and (max-width: 400px) {
  .mp-link{
    margin: 0px -20px 0px -20px;
    scale: 0.9;
}
.mp-nav{
 
 text-align: center;
 
}
.lact{
  padding-top: 5px !important;
  padding-bottom: -10px !important;
  margin-bottom: 15px !important;
}
.mp-main{
 width: 90%;
}
}
@media only screen and (max-width: 380px) {
  .mp-link{
    margin: 0px -22px 0px -22px;
    scale: 0.9;
}

}
.mp-head h2{
    font-size: 28px;
    font-weight: 600;
}

         
.lact {
    
    
    background-image: linear-gradient(to right, #B3FFAB 0%, #12FFF7  51%, #B3FFAB  100%);
    margin: 10px;
    border-radius: 28px;
    
   
    text-decoration: none;
    padding: 1px;
    text-align: center;
    
    transition: 0.5s;
    background-size: 200% auto;
    color: white;            
    box-shadow: 0 0 20px #d0d0d0;
    
   
    
  }

  .lact:hover {
    background-position: right center; /* change the direction of the change here */
    color: #fff;
    text-decoration: none;
  }
  .mp-link:active{
    color: coral;
   
  }

  .mp-head-dark{
    color: rgba(239, 239, 239, 0.682);

  }
  .mp-head-light{
    color: rgba(124, 124, 124, 0.682);

  }
 
  

 