    }
  }
  
    
    
  
  .but{
    font-family: 'Roboto', sans-serif;
    position: relative;
    text-decoration: none;
    color: #4e4e4e;
    letter-spacing: 3px;
    margin-right: 10%;
    padding: 10px 20px;
    
    
  }
  .but:hover{
    transition: all 0.5s ease ;
    color: white;
  }

  .btn-3{
   width: 20px;
    color:white;
    transform: skew(20deg, 0deg) scale(1.1);
    transition: all 0.4s ease;
  
}
.btn-3:hover{
   cursor: pointer;
}


.btn-3::after{
    content: "";
    position: absolute;
    width: 10%;
    height: 100%;
    left: -9%;
    top: 0;
    transform: skew(20deg, 0deg);
    background-color: rgb(199, 199, 199);
    z-index: 10;
    transition: all 0.4s ease;
    
}

.btn-3::before{
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    transform: skew(20deg, 0deg);
    background-color: #2BF969;
    border: 3px solid #2BF969;
    border-radius: 0px 10px 10px 0px;
    transition: all 0.4s ease;
    z-index: -1;
    
}

.btn-3:hover::after{
    background-color: rgba(148, 148, 148, 0.205);
    transform: skew(20deg, 0deg) translateX(130px);
    
    
}

.btn-3:hover::before{
    background-color: transparent;
    border-radius: 10px 0px 0px 10px;
    
    transform: skew(20deg, 0deg) scale(1.1);

}
.btn-3:hover{
  color: #4e4e4e;
}


.btn-4{
  font-family: 'Roboto', sans-serif;
  position: relative;
  text-decoration: none;
 
  letter-spacing: 3px;
  margin-right: 10%;
  padding: 10px 20px;
  color:rgb(255, 255, 255);
  transform: skew(20deg, 0deg) scale(1.1);
  transition: all 0.4s ease;
  z-index: 50;
}
.btn-4:hover{
  color: white !important;
}

.btn-4::after{
  content: "";
  position: absolute;
  width: 70%;
  height: 100%;
  left: -7%;
  top: 0;
  border-radius: 0px 10px 0px 10px;
  transform: skew(20deg, 0deg);
  background-color: rgb(199, 199, 199);
  z-index: -1;
  transition: all 0.4s ease;
  
}

.btn-4::before{
  
  content: "";
  position: absolute;
  width: 80%;
  height: 100%;
  left: 0;
  top: 0;
  transform: skew(20deg, 0deg);
  border: 3px solid #2BF969;
  border-radius: 0px 10px 10px 0px;
  transition: all 0.4s ease;
  z-index: -1;
  
}

.btn-4:hover::after{
  width: 40%;
  background-color: rgba(148, 148, 148, 0.205);
  transform: skew(20deg, 0deg) translateX(130px);
  border-radius: 0px 10px 0px 10px;
  
  
}

.btn-4:hover::before{
  background-color: #2BF969;
  border-radius: 0px 10px 10px 10px;
  
  transform: skew(20deg, 0deg) scale(1.105);

}


